import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import toast from 'components/common/toast'
import VueLazyLoad from 'vue-lazyload'

Vue.config.productionTip = false
Vue.prototype.$bus = new Vue() //给vue实例添加一个$bus,事件总线

Vue.use(toast)
Vue.use(VueLazyLoad,{
  loading:require('./assets/img/menhera/142.png')
  })
new Vue({
  render: h => h(App),
  router,
  store
}).$mount('#app')
